<template>
  <div class="hs-container">
    <c-header isBackNav :title="`${$t('base.AirTicketsBooking')}-${Number(intlFlightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}`"></c-header>
    <hs-scroll
      class="table-view"
      ref="tableView">
      <ul>
        <hs-section backgroundColor="#F6F7F8">
          <div class="custom-cell-segments">
            <div class="segments" @click="_openSegmentDetailsDialog">
              <div class="details">
                <div class="item" v-for="(item, index) in intlFlightOrder.SelectedSegment" :key="index">
                  <div class="mark">{{_showSegmentTitle(index)}}</div>
                  <p>{{item.DepartDate}}</p>
                  <p>{{item.DepartDayOfWeek}}</p>
                  <p>{{item.DepartDateTime.split(' ')[1]}}</p>
                  <p>{{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}-{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}</p>
                </div>
              </div>
              <div class="logo" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
            </div>
            <div class="infor" v-if="details && details.ChargeInfoList">
              <div>
                <p>{{$t('base.AirTickets')}}<span>¥{{details.ChargeInfoList[0].Price}}</span></p>
                <p>{{$t('base.Tax')}}<span>¥{{details.ChargeInfoList[0].Tax}}</span></p>
                <p v-if="details.ChargeInfoList[0].Rebate > 0">{{$t('base.FrontReturn')}}<span>-¥{{details.ChargeInfoList[0].Rebate}}</span></p>
              </div>
              <p>退改签 ></p>
            </div>
          </div>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="passenger-title" :type="language">
              <div>
                <p>{{$t('base.PassengerInformation')}}</p>
                <span>{{$t('base.OnlyAdultTicketBookingsAreSupported')}}</span>
              </div>
              <button @click="$refs.CPassenger.open()">{{$t('base.Add')}} +</button>
            </div>
          </hs-cell-1>
          <hs-cell-1 v-for="(item, index) in intlFlightOrder.Passengers" :key="`passengers${index}`">
            <div class="passenger-infor" :style="{transform: `translateX(${!intlFlightOrder.Passengers[index].openDelete ? '0px' : '-65px'})`}">
              <div class="left" @click="_setPassengerOpenDelete(true, index)">
                <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
              </div>
              <div class="right" @click="_gotoPassengerDetails(index)">
                <div class="item">
                  <p class="name" v-if="item.DefaultNameType === 0">{{item.ChName}}</p>
                  <p class="name" v-else>{{item.LastName}}&nbsp;{{item.FirstName}}</p>
                </div>
                <div class="item" v-if="item.Credentials && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0] && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo">
                  <p class="other-title">{{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialName}}</p>
                  <p class="other-value">{{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo}}</p>
                </div>
                <div class="item">
                  <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                  <p class="other-value">{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
                <div class="delete" @click.stop="_deletePassenger(index)">删除</div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 value="目前线上保险仅支持购买第一程" :showLogo="false"></hs-cell-1>
          <hs-cell-1 v-for="(item, index) in details.InsuranceProducts" :key="index"
            :showLogo="false"
            @click.native="_selectInsurance(item, index)">
            <div class="insure">
              <div class="details">
                <div class="title">
                  <p>{{item.ClientShowName}}&nbsp;|&nbsp;{{_showSegmentTitle(0)}}</p>
                  <div class="logo" style="background-image: url('static/image/plane-ticket/book/insure_icon.png')"></div>
                  <p>￥{{item.SalePrice}}/人</p>
                </div>
              </div>
              <div
                v-if="(((item.InsuranceType === 1 && details.IsFreeInsurance) ||
                (item.InsuranceType === 2 && details.IsBindAccidentInsurance)))"
                class="logo" style="background-image: url('static/image/base/check-box/check-box-disabled.png')"></div>
              <div v-else-if="item.select === false" class="logo" style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
              <div v-else class="logo" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="insure">
              <div class="details">
                <div class="title">
                  <p v-if="details.ChargeInfoList && details.ChargeInfoList[0]">{{$t('base.ServiceFee')}}&nbsp;￥{{details.ChargeInfoList[0].ServicePrice}}{{$t('base.PreQuantifier')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 其他信息 -->
        <hs-section>
          <hs-cell-small-title v-if="details.IsDisplayCustomItem" @click.native="showCCustomItem = true"
            :title="details.CustomItemName"
            type="disabled-input"
            :value="intlFlightOrder.CustomItem ? intlFlightOrder.CustomItem.Name : ''"
            :placeholder="`(${details.IsRequiredCustomItem ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.IsDisplayPurpose"
            :title="$t('base.TravelPurpose')"
            type="input"
            :value="intlFlightOrder.Purpose" @input="_setFlightOrder(arguments[0], ['Purpose'])"
            :placeholder="`(${details.IsRequiredPurpose ? $t('base.RequiredFill'): $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.IsDisplayAuthorizationCode"
            :title="$t('base.AuthorizationCode')"
            type="input"
            :value="intlFlightOrder.AuthorizationCode" @input="_setFlightOrder(arguments[0], ['AuthorizationCode'])"
            :placeholder="`(${details.IsRequiredAuthorizationCode ? $t('base.RequiredFill'): $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.VettingType === 3 && details.EnableVetting" @click.native="picker.show()"
            :title="$t('base.Approver')"
            type="disabled-input"
            :value="intlFlightOrder.AppointVettingPersonName"
            :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(intlFlightOrder.TravelType) === 0" @click.native="payTypePicker.show()"
            :title="$t('base.PaymentWay')"
            type="disabled-input"
            :value="intlFlightOrder.PayType | filterPayType">
          </hs-cell-small-title>
        </hs-section>

        <!-- 联系人 -->
        <hs-section>
          <hs-cell-1 :value="$t('base.ContactInformation')" logoName="add.png" @click.native="$refs.CFrequentContacts.open()"></hs-cell-1>
          <hs-cell-1 v-for="(item, index) in intlFlightOrder.Contacts" :key="index">
            <div class="passenger-infor" :style="{transform: `translateX(${!intlFlightOrder.Contacts[index].openDelete ? '0px' : '-65px'})`}">
              <div class="left" @click="_setContactsOpenDelete(true, index)">
                <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
              </div>
              <div class="right" @click="_gotoContactsDetails(index)">
                <div class="item">
                  <p class="name">{{item.Name}}</p>
                </div>
                <div class="item">
                  <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                  <p class="other-value">{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
                <div class="item">
                  <p class="other-title">{{$t('base.Email')}}</p>
                  <p class="other-value">{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
                <div class="delete" @click.stop="_deleteContact(index)">{{$t('base.Delete')}}</div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>

    <!-- 价格明细 -->
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p>￥{{initSum * intlFlightOrder.Passengers.length}}</p>
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
      <hs-gradient-button @click="_nextStep"
        type="common"
        width="auto"
        :title="$t('base.NextStep')">
      </hs-gradient-button>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :isListenScroll="true" @scrollStart="_scrollStart" @click.native.stop>
        <ul>
          <hs-section v-if="details.ChargeInfoList && details.ChargeInfoList[0]">
            <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Price >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.AirTickets')}}</p>
                <p><span>￥{{details.ChargeInfoList[0].Price}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Tax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.Tax')}}</p>
                <p><span>￥{{details.ChargeInfoList[0].Tax}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Rebate > 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FrontReturn')}}</p>
                <p><span>-￥{{details.ChargeInfoList[0].Rebate}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].ServicePrice >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.ServiceFee')}}</p>
                <p><span>￥{{details.ChargeInfoList[0].ServicePrice}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 v-for="(item, index) in details.InsuranceProducts.filter(v => v.select)" :key="index"
              :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>{{item.ClientShowName}}&nbsp;|&nbsp;{{_showSegmentTitle(0)}}</p>
                <p><span>￥{{item.SalePrice}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.Copy')}}</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <div class="show-more" ref="show-more">{{$t('base.SlideUpToSeeMore')}}</div>
    </hs-dialog>

    <!-- 航班详情 -->
    <hs-dialog v-model="showSegmentDetailsDialog" ref="SegmentDetails">
      <div class="segmentDetails" @click.native.stop>
        <div class="header">
          <div class="logo"></div>
          <p>航班详情</p>
          <div @click="$refs.SegmentDetails.showDetails = false"
            class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')"></div>
        </div>
        <hs-scroll
          class="details"
          ref="segmentTableView"
          :data="[intlFlightOrder.SelectedSegment]">
          <ul>
            <div class="item" v-for="(item, index) in intlFlightOrder.SelectedSegment" :key="index">
              <div class="item-header">
                <div class="mark">{{_showSegmentTitle(index)}}</div>
                <p>{{item.DepartDate}}</p>
                <p>{{item.DepartDayOfWeek}}</p>
                <p>{{item.DepartDateTime.split(' ')[1]}}</p>
                <p>{{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}-{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}</p>
              </div>
              <segment :datalist="item.FlightLegs"></segment>
            </div>
          </ul>
        </hs-scroll>
      </div>
    </hs-dialog>

    <!-- 乘客列表 -->
    <c-passenger
      ref="CPassenger"
      :XOrder="intlFlightOrder"
      :BookInit="details"
      :OrderBusinessType="6"
      @delegateWithConfirm="_delegateWithConfirm"
    ></c-passenger>

    <!-- 乘客详情 -->
    <passenger-details
      ref="PassengerDetails"
      :details="editingPassenger"
      :BookInit="details"
      :OrderBusinessType="6"
      @delegate="_delegateWithPassengerDetails"
    ></passenger-details>

    <!-- 自定义项列表 -->
    <c-custom-item :show.sync="showCCustomItem" :orderInit.sync="details" @delegate="_delegateWithCustomItem"></c-custom-item>

    <!-- 联系人详情 -->
    <c-contacts :show.sync="showCContacts" v-model="contactDetails" :datalist="intlFlightOrder.Contacts" :OrderBusinessType="6" @delegate="_delegateWithContacts"></c-contacts>

    <!-- 常用联系人列表 -->
    <c-frequent-contacts ref="CFrequentContacts" :orderInit.sync="details" :datalist="intlFlightOrder.Contacts" :OrderBusinessType="6" @delegate="_delegateWithFrequentContacts"></c-frequent-contacts>

  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import CCustomItem from 'components/c-passenger/c-custom-item'
import CContacts from 'components/c-passenger/c-contacts'
import CFrequentContacts from 'components/c-passenger/c-frequent-contacts'
import CHeader from 'components/c-header/c-header'
import CPassenger from 'components/c-passenger/c-passenger'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import PassengerDetails from 'pages/personal/passengers/details.vue'
import Segment from '../infor-list/segment'
import {mapGetters, mapMutations} from 'vuex'
import {kpiIntlflightGetBookInitInfo, kpiIntlflightCheckOrderRepeat, kpiIntlflightSaveIntlOrder} from 'api'

const PAY_TYPE_PICKER_DATA_1 = { text: Vue.prototype.$t('base.CompanyPayment'), value: 1 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_2 = { text: Vue.prototype.$t('base.PersonalPayment'), value: 2 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_3 = { text: `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`, value: 3 } // eslint-disable-line

export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 检查数据完整性
      if (vm.noValue(vm.intlFlightOrder.TravelType)) {
        vm.$router.replace('/home')
        return
      }
      vm._getDetails()
    })
  },
  beforeRouteLeave(to, from, next) {
    if (this.$refs.CPassenger && this.$refs.CPassenger.show) {
      this.$refs.CPassenger.close()
      next(false)
    } else if (this.$refs.PassengerDetails && this.$refs.PassengerDetails.show) {
      this.$refs.PassengerDetails.close()
      next(false)
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters([
      'intlFlightOrder'
    ])
  },
  data() {
    return {
      details: {},
      extend: false,
      /** 乘客 */
      editingPassenger: {},
      /********************************/
      showDetailsDialog: false,
      /** 航班详情 */
      showSegmentDetailsDialog: false,
      /** 自定义项 */
      showCCustomItem: false,
      /** 联系人 */
      showCContacts: false,
      contactDetails: {},
      /********************************/
      initSum: 0
    }
  },
  watch: {
    details: {
      handler: function(val, oldVal) {
        let sum = 0
        sum += this.details.ChargeInfoList[0].Price
        sum += this.details.ChargeInfoList[0].Tax
        sum += this.details.ChargeInfoList[0].ServicePrice
        sum -= this.details.ChargeInfoList[0].Rebate

        // 保险费
        if (this.details.InsuranceProducts) {
          this.details.InsuranceProducts.forEach(item => {
            if (item.select) {
              sum += item.SalePrice
            }
          })
        }

        this.initSum = sum
      },
      deep: true
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_INTL_FLIGHT_ORDER'
    ]),
    _getDetails() {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments = res.SearchParams.Segments.map(v => {
        v.DepartureTime = this.beTimeNumber(v.DepartureTime)
        return v
      })
      let params = {
        SearchParams: res.SearchParams,
        SelectedSegmentIDs: res.SelectedSegmentIDs,
        SelectedFareInfoID: res.SelectedFareInfoID,
        TravelType: res.TravelType
      }
      kpiIntlflightGetBookInitInfo(params).then((res) => {
        console.log('获取国际订单填写页初始信息: ', res)
        this.details = res.ResultData
        this._initData()
      })
    },

    /** 获取完接口后初始化一些信息 */
    _initData() {
      // 默认联系人
      if (!this.noValue(JSON.stringify(this.details.DefaultContacts))) {
        let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
        res.Contacts = [Object.assign(this.details.DefaultContact, {openDelete: false})]
        this.SET_INTL_FLIGHT_ORDER(res)
      }
      // =====================================================

      // 初始化保险select
      if (this.details.InsuranceProducts) {
        this.details.InsuranceProducts.forEach((element, index) => {
          if ((element.InsuranceType === 1 && this.details.IsFreeInsurance) || (element.InsuranceType === 2 && this.details.IsBindAccidentInsurance)) {
            this.$set(this.details.InsuranceProducts[index], 'select', true)
          } else {
            this.$set(this.details.InsuranceProducts[index], 'select', false)
          }
        })
      }
      // ==================================================================
      this._creatPayTypePicker(this.details.PayTypes)
      this._creatApproversPicker(this.details.Approvers)
    },

    /** 根据航段显示单程/往返程/N程 */
    _showSegmentTitle(index) {
      let length = this.intlFlightOrder.SelectedSegment && !this.noValue(JSON.stringify(this.intlFlightOrder.SelectedSegment)) ? this.intlFlightOrder.SelectedSegment.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.intlFlightOrder.SelectedSegment[0].DepartAirportName === this.intlFlightOrder.SelectedSegment[1].ArrivalAirportName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },

    // 乘客
    /** 添加乘客回调 */
    _delegateWithConfirm(datalist) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Passengers = datalist
      this.SET_INTL_FLIGHT_ORDER(res)
      this.$refs.CPassenger.open()
    },

    /** 乘客信息，待删除状态 */
    _setPassengerOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Passengers[index].openDelete = value
      this.SET_INTL_FLIGHT_ORDER(res)
    },

    /** 去乘客详情 */
    _gotoPassengerDetails(index) {
      if (this.intlFlightOrder.Passengers[index].openDelete) {
        this._setPassengerOpenDelete(false, index)
      } else {
        this.editingPassenger = JSON.parse(JSON.stringify(this.intlFlightOrder.Passengers[index]))
        this.$set(this.editingPassenger, 'index', index)
        this.$refs.PassengerDetails.open()
      }
    },

    /** 删除乘客 */
    _deletePassenger(index) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Passengers.splice(index, 1)
      this.SET_INTL_FLIGHT_ORDER(res)
    },

    /** 修改入住人回调 */
    _delegateWithPassengerDetails(passenger) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Passengers.splice(passenger.index, 1, passenger)
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    // ============================================
    // 保险
    _selectInsurance(item, index) {
      if (((item.InsuranceType === 1 && this.details.IsFreeInsurance) || (item.InsuranceType === 2 && this.details.IsBindAccidentInsurance)) && (item.Order === 1)) {
      } else {
        this.details.InsuranceProducts[index].select = !item.select
      }
    },
    // ==========================================
    // 联系人
    _setContactsOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Contacts[index].openDelete = value
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _gotoContactsDetails(index) {
      if (this.intlFlightOrder.Contacts[index].openDelete) {
        this._setContactsOpenDelete(false, index)
      } else {
        this.contactDetails = Object.assign(JSON.parse(JSON.stringify(this.intlFlightOrder.Contacts[index])), {index: index})
        this.showCContacts = true
      }
    },
    _deleteContact(index) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Contacts.splice(index, 1)
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _delegateWithContacts(contacts) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Contacts.splice(contacts.index, 1, Object.assign(contacts, {openDelete: false}))
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _delegateWithFrequentContacts(val) {
      console.log('选中的联系人: ', val)
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Contacts = val
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    // =============================================

    /** 自定义项回调 */
    _delegateWithCustomItem(item) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.CustomItem = item
      this.SET_INTL_FLIGHT_ORDER(res)
    },

    // 费用明细
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        // console.log(this.$refs.detailsDialog)
        this.showDetailsDialog = true
        setTimeout(() => {
          let dom = this.$refs.detailsDialog.$children[0].scroll
          if (dom.wrapperHeight < dom.scrollerHeight) {
            this.$refs['show-more'].style.opacity = 1
          } else {
            this.$refs['show-more'].style.opacity = 0
          }
        }, 400)
      }
    },

    // 审批人picker
    _creatApproversPicker(Approvers) {
      let data = []
      if (Approvers) {
        Approvers.forEach(element => {
          data.push({
            text: element.Name,
            value: element.ID
          })
        })
      }
      this.picker = this.$createPicker({
        title: this.$t('base.SelectApprover'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._selectApproversPickerHandle
      })
    },
    _selectApproversPickerHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.AppointVettingPersonID = val[0]
      res.AppointVettingPersonName = text[0]
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    // =========================================

    // 初始化支付方式picker
    _creatPayTypePicker(PayTypes) {
      let data = []
      if (Number(this.intlFlightOrder.TravelType) === 1) {
        data = [PAY_TYPE_PICKER_DATA_2]
      } else if (PayTypes) {
        PayTypes.forEach(element => {
          if (element === 1) {
            data.push(PAY_TYPE_PICKER_DATA_1)
          } else if (element === 2) {
            data.push(PAY_TYPE_PICKER_DATA_2)
          } else if (element === 3) {
            data.push(PAY_TYPE_PICKER_DATA_3)
          }
        })
      }
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.PayType = data[0] ? data[0].value : null
      this.SET_INTL_FLIGHT_ORDER(res)
      this.payTypePicker = this.$createPicker({
        title: this.$t('base.PaymentWay'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._payTypePickerSelectHandle
      })
    },
    _payTypePickerSelectHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.PayType = val[0]
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    // ===========================================================

    /** 打开行程详情 */
    _openSegmentDetailsDialog() {
      this.showSegmentDetailsDialog = true
      setTimeout(() => {
        this.$refs.segmentTableView.forceUpdate()
      }, 500)
    },

    /** flightOrder双向绑定传值 这个方法可以优化 */
    _setFlightOrder(val, keys) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      let length = keys.length
      if (length === 1) {
        res[keys[0]] = val
      } else if (length === 2) {
        res[keys[0]][keys[1]] = val
      } else if (length === 3) {
        res[keys[0]][keys[1]][keys[2]] = val
      } else if (length === 4) {
        res[keys[0]][keys[1]][keys[2]][keys[3]] = val
      }
      this.SET_INTL_FLIGHT_ORDER(res)
    },

    /** 点击下一步后, 流程 */
    _nextStep() {
      // 为乘机人绑定第一程保险
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.Passengers.map(v => {
        v.Insurances = this.details.InsuranceProducts.filter(f => f.select).map(m => {
          return {
            ID: m.ID,
            SalePrice: m.SalePrice,
            InsuranceType: m.InsuranceType,
            InsuranceCategory: m.ClientShowName,
            Count: 1,
            Order: 1
          }
        })
        return v
      })
      this.SET_INTL_FLIGHT_ORDER(res)

      this._localVerify()
    },
    // 1. 本地验证
    _localVerify() {
      // 1.1 填写初始化接口数据
      if (this.noValue(JSON.stringify(this.details))) {
        this.$toast(this.$t('TM.TheSysteIsBusyPleaseReOperate'))
        this.$router.replace('/home')
        return
      }

      // 1.2 乘客数量
      if (this.intlFlightOrder.Passengers.length === 0) {
        this.$toast(this.$t(`TM.PleaseSelect`, 'function', this.$t('base.Passenger')))
        return
      }

      // 1.3 乘客详情_成本中心/部门/BU/年龄>=12/证件号/证件有效期
      for (let item of this.intlFlightOrder.Passengers) {
        if (this.details.IsRequiredCostCenter && this.noValue(JSON.stringify(item.CostCenter))) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.$t('base.CostCenter')}))
          return
        } else if (this.details.IsRequiedDepartment && this.noValue(JSON.stringify(item.Department))) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.$t('base.Department')}))
          return
        } else if (this.details.IsRequiredBusinessUnit && this.noValue(JSON.stringify(item.BusinessUnit))) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.details.BusinessUnitName}))
          return
        } else if (item.Credential.CredentialType === 1 && this.getAge(this.beTime(this.calBirthDate(item.Credential.CredentialNo))) < 12) {
          this.$toast(this.$t(`TM.XIsUnderXyearsOld`, 'function', item.Name))
          return
        } else if (item.Credential.CredentialType !== 1 && item.Birthday && this.getAge(item.Birthday) < 12) {
          this.$toast(this.$t(`TM.XIsUnderXyearsOld`, 'function', item.Name))
          return
        } else if (this.noValue(JSON.stringify(item.Credential)) || this.noValue(item.Credential.CredentialNo)) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.$t('base.ID')}))
          return
        } else if (this.noValue(JSON.stringify(item.Credential)) || this.noValue(item.Credential.EffectiveDate)) {
          this.$toast(this.$t(`TM.PleaseSelectX`, 'function', {name: item.Name, thing: this.$t('base.EffectiveDate')}))
          return
        }
      }

      // 1.4 根据证件号判断乘客不得重复
      if (this.isRepeat(this.intlFlightOrder.Passengers.map(v => v.Credential.CredentialNo))) {
        this.$toast(this.$t(`TM.ThePersonsIDNumberCannotBeRepeated`))
        return
      }

      // 1.5 自定义项
      if (this.details.IsRequiredCustomItem && this.noValue(JSON.stringify(this.intlFlightOrder.CustomItem))) {
        this.$toast(this.$t(`TM.PleaseSelect`, 'function', this.details.CustomItemName))
        return
      }

      // 1.6 差旅目的
      if (this.details.IsRequiredPurpose && this.noValue(this.intlFlightOrder.Purpose)) {
        this.$toast(this.$t(`TM.PleaseFill`, 'function', this.$t('base.TravelPurpose')))
        return
      }

      // 1.7 授权码
      if (this.details.IsRequiredAuthorizationCode && this.noValue(this.intlFlightOrder.AuthorizationCode)) {
        this.$toast(this.$t(`TM.PleaseFill`, 'function', this.$t('base.AuthorizationCode')))
        return
      }

      // 1.8 联系人数量
      if (this.intlFlightOrder.Contacts.length === 0) {
        this.$toast(this.$t(`TM.PleaseSelect`, 'function', this.$t('base.Contacts')))
        return
      }

      // 1.9 联系人详情
      for (let item of this.intlFlightOrder.Contacts) {
        if (this.noValue(item.Mobile)) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
          return
        } else if (this.noValue(item.Email) && (item.IsSendBookEmail || item.IsSendIssuedEmail || item.IsSendConfirmEmail)) {
          this.$toast(this.$t(`TM.PleaseFillX`, 'function', {name: item.Name, thing: this.$t('base.Email')}))
          return
        }
      }

      // 1.10 根据手机号判断联系人不得重复
      let maplist = new Map()
      if (this.intlFlightOrder.Contacts.filter(a => !maplist.has(a.Mobile) && maplist.set(a.Mobile, 1)).length < this.intlFlightOrder.Contacts.length) {
        this.$toast(this.$t('TM.ThereMustBeNoContactWithTheSamePhoneNumber'))
        return
      }

      /** 填写姓名 */
      let intlFlightOrder = JSON.parse(JSON.stringify(this.intlFlightOrder))
      intlFlightOrder.Passengers.forEach((element, index) => {
        intlFlightOrder.Passengers[index].Name = element.DefaultNameType === 0 ? element.ChName : `${element.LastName} ${element.FirstName}`
      })
      this.SET_INTL_FLIGHT_ORDER(intlFlightOrder)

      this._checkOrderRepeat()
    },

    // 2. 校验乘客同时段是否有其他行程
    _checkOrderRepeat() {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments = res.SearchParams.Segments.map(v => {
        v.DepartureTime = this.beTimeNumber(v.DepartureTime)
        return v
      })
      let params = {
        Passengers: res.Passengers,
        SearchParams: res.SearchParams,
        SelectedSegmentIDs: res.SelectedSegmentIDs,
        SelectedFareInfoID: res.SelectedFareInfoID
      }
      kpiIntlflightCheckOrderRepeat(params).then((res) => {
        console.log('校验乘客同时段是否有其他行程: ', res)
        if (res.ResultData.IsRepeat) {
          this.$alert({
            content: res.ResultData.OrderRepeatText,
            cancelTxt: this.$t('base.Cancel'),
            confirmFn: () => {
              this._saveOrder()
            }
          })
        } else {
          this._saveOrder()
        }
      })
    },

    // 3. 保存订单
    _saveOrder() {
      let params = JSON.parse(JSON.stringify(this.intlFlightOrder))
      params.SearchParams.Segments = params.SearchParams.Segments.map(v => {
        v.DepartureTime = this.beTimeNumber(v.DepartureTime)
        return v
      })
      params.TotalPrice = this.initSum * params.Passengers.length
      kpiIntlflightSaveIntlOrder(params).then((res) => {
        console.log('保存订单: ', res)
        let intlFlightOrder = JSON.parse(JSON.stringify(this.intlFlightOrder))
        intlFlightOrder.TotalPrice = params.TotalPrice
        intlFlightOrder.OrderIDs = res.ResultData.OrderIDs
        intlFlightOrder.VettingProcessList = res.ResultData.VettingProcessList
        intlFlightOrder.PageBtnInfo = res.ResultData.PageBtnInfo
        this.SET_INTL_FLIGHT_ORDER(intlFlightOrder)
        this._gotoDetails()
      })
    },

    // 4. 前往确认页面
    _gotoDetails() {
      this.$router.push({name: 'intlPlaneTicketOrderConfirm', params: {details: this.details}})
    },
    /**********************************************************************************/
    // 监听明细
    _scrollStart() {
      this.$refs['show-more'].style.opacity = 0
    }
  },
  components: {
    CCustomItem,
    CContacts,
    CFrequentContacts,
    CHeader,
    CPassenger,
    HsButton,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    HsGradientButton,
    HsScroll,
    HsSection,
    PassengerDetails,
    Segment
  }
}
</script>
