        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.infor
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        font-size 0.8rem
        >div
          @extend .flex-column
          @extend .flex-top
          width calc(100% - 80px)
          >p
            margin-bottom 6px
            &:last-child
              margin-bottom 0
            >span
              color $color_theme
              margin-left 6px
        >p
          width 80px
          color $color_gray_text
          text-align right

    // 乘客信息
    .passenger-title
      @extend .flex-row
      @extend .flex-between
      width 100%
      >div
        @extend .flex-row
        @extend .flex-start
        >span
          font-size 0.7rem
          color $color_gray_text
          margin-left 5px
      >button
        font-size 0.8rem
        padding 5px 10px
