        border-radius 4px
        border 1px solid $color_theme
        background-color $color_white
    .passenger-title[type="EN"]
      padding 10px 0
      >div
        @extend .flex-column
        @extend .flex-top
        >p
          margin-bottom 5px
        >span
          margin-left 0
    .passenger-infor
      @extend .flex-row
      @extend .flex-start
      @extend .no-wrap
      width 100%
      padding 10px 0
      transition all 400ms
      >.left
        @extend .flex-center
        width 60px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.right
        width calc(100% - 60px)
        position relative
        >.item
          @extend .flex-row
          @extend .flex-between
          width 100%
          margin 5px 0
          >.name
            color $color_black_text_0
            font-weight bold
            line-height 1.4em
            width: 100%
            margin-bottom 3px
          >.other-title
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width 60px
          >.other-value
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width calc(100% - 70px)
        >.delete
          @extend .flex-center
          position absolute
          width 60px
          // height 100%
          background-color $color_theme
          color $color_white
          font-size 0.9rem
          top -11px
          right -100px
          bottom -11px
      &:last-child
        >.right
          >.delete
            bottom -10px
    .insure
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 10px 0
      >.details
        width calc(100% - 30px)
        >.title
          @extend .flex-row
          @extend .flex-start
          font-size 0.8rem
          color $color_black_text_0
          margin-bottom 3px
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            margin 0 10px 0 5px
        >.little-title
          font-size 0.7rem
          color $color_gray_text
      >.logo
        width 20px
        height 20px
        background-size 100% 100%

  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width 50%
      height 100%
      >p
        font-size 1rem
        color $color_theme
        font-weight bold
        margin-right 5px
      >.logo
        width 20px
        height 20px
