        &:last-child
          border-bottom-width 0
        >.item-header
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          >.mark
            font-size 1rem
            color $color_theme
            font-weight bold
            padding 5px
            border-radius 2px
          >p
            font-size 1rem
            margin-left 5px
